.navbar {
    min-height: 60px;
    background-color: #4e5a62;
  }
  
  .navbar-brand {
    padding: 3px 15px;
    height: 60px;
    line-height: 60px;
  }
  
  .navbar-toggle {
    /* (80px - button height 34px) / 2 = 23px */
    margin-top: 13px;
    padding: 9px 10px !important;
  }
  
  @media (min-width: 768px) {
    .navbar-nav > li > a {
      /* (80px - line-height of 27px) / 2 = 26.5px */
      padding-top: 16.5px;
      padding-bottom: 16.5px;
      line-height: 27px;
    }
  }
body{
    overflow-x: hidden;
}

.carousel {
    height: 400px;
    z-index: -1;
}

.carousel-inner img {
    width: 100%;
    height: 400px;
    background-size: cover;
}

.slide-shadow {
    position: absolute;
    height: 400px;
    width: 100%;
    top: 0;
    opacity: 0.5;
    background-color: #000;
    z-index: 0;
}

.inner-content {
    position: absolute;
    top: 20%;
    width: 100%;
    color: #fff;
}

.inner-content > h4 {
    font-size: 20px;
}

.inner-content > h1 {
    font-size: 25px;
}

.button {
    background-color: #ed2f59;
    outline-style: solid #ed2f59;
}

.top-second-row {
    height: 18rem;
}

.second-row-img {
    height: 100%;
    width: 100%;
    background-size: cover;
}

.centered {
    position: absolute;
    top: 4rem;
    left: 25px;
    right: 25px;
    color: #fff;
    font-size: 20px;
    height: auto;
}

.second-row-shadow {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #4e5a62;
    opacity: 0.5;
    top: 0;
}

.row .top-second-row {
    padding: 0;
}

.top-third-row {
    height: 450px;
    width: 100%;    
}

.third-row-img {
    height: 100%;
    width: 100%;
}

.third-row-shadow {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.5;
    top: 0;
}

.row .top-third-row {
    padding: 0;
}

.abt {
    position: absolute;
    top: 0;
    left: 25px;
    right: 25px;
    left: 0;
    right: 0;
    bottom: 0;
}

.top-fourth-row {
    position: relative;
    height: 870px;
    width: 100%;
}

.fourth-row-img {
    height: 100%;
    width: 100%;
    background-size: cover;
}

.fourth-row-shadow {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: 0.7;
}

.fourth-row-content {
    position: absolute;
    top: 2rem;
}

.top-fifth-row {
    position: relative;  
    height: 121.7rem;  
}

.fifth-row-shadow {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    opacity: 0.5;
    background-color: #000;
    z-index: 1;
}

#carouselSlide {
    height: 122rem;
    width: 100%;
}

.carousel-inn img {
    height: 122rem;
    width: 100%;
    background-size: cover;
}

.services {
    position: absolute;
    top: 2rem;
    color: #fff;
}

.fifth-row-content {
    position: absolute;
    top: 8rem;
    z-index: 2;
}
.serve-card {
    width: 100%;
}
.card-img-top {
    height: 11rem;
}

.top-sixth-row {
    position: relative;
    height: 482px;
    width: 100%;  
}

.top-seventh-row {
    position: relative;
    height: 670px;
    width: 100%;
}

.seventh-row-img {
    height: 100%;
    width: 100%;
    background-size: cover;
}

.seventh-row-shadow {
    position: absolute;
