    width: 100%;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

@media only screen and (min-width: 768px) and  (max-width: 1023px) {
    .inner-content {
        top: 15%;
    }

    .top-fourth-row {
        height: 440px;
    }

    .fourth-row-content {
        top: 3rem;
    }

    .top-fifth-row {
        height: 57rem;
        width: 100%;
    }

    #carouselSlide {
        height: 57rem;
    }

    .carousel-inn img {
        height: 57rem;
        width: 100%;
        background-size: cover;
    }

    .fifth-row-content {
        position: absolute;
        top: 7rem;
        width: 100%;
    }

    .services {
        width: 100%;
    }

    .card-img-top {
        height: 8rem;
    }

    .top-sixth-row {
        height: 400px;
    }

    .top-seventh-row {
        height: 560px;
    }

    .seventh-row-shadow {
        height: 560px;
    }

    .seventh-row-content {
        top: 3rem;
        padding-left: 30%;
    }

    .seventh-row-form {
        top: 8rem;
    }

    .top-eighth-row {
        height: 330px;
    }
}

@media only screen and (min-width: 1024px){
    .carousel {
        height: 790px;
    }

    .carousel-inner img {
        height: 790px;
    }

    .slide-shadow {
        height: 790px;
    }

    .inner-content {
        top: 20rem;
    }

    .inner-content > h4 {
        font-size: 30px;
    }

    .inner-content > h1 {
        font-size: 50px;
    }

    .top-third-row {
        height: 400px;
    }

    .abt > p {
        font-size: 18px;
    }

    .top-fourth-row {
        height: 400px;
    }

    .fourth-row-content2 > p {
        font-size: 20px;
    }

    .fourth-row-content {
        top: 4rem;
    }

    .top-fifth-row {
        height: 60rem;
    }

    #carouselSlide {
        height: 60rem;
    }

    .carousel-inn img {
        height: 60rem;
    }

    .services {
        margin-left: 22rem;
    }

    .top-sixth-row {
        height: 380px;
    }

    .seventh-row-content {
        top: 4rem;
        padding-left: 0;
        margin-left: 27rem;
    }

    .seventh-row-form {
        top: 10rem;
        padding-right: 0;
        width: 72%;
    }

    .top-eighth-row {
        height: 330px;
    }
}
